.ctstrg-1 {
    padding-left: 30px;
}

button.ctstrg-btn {
    padding: 6px 10px 6px !important;
    box-shadow: inset 0 1px 0 rgb(255 255 255 / 20%),
    0 1px 2px rgb(0 0 0 / 5%);
    border: 1px solid;
    border-radius: 4px;
    border-color: rgba(0, 0, 0, 0.25) rgba(0, 0, 0, 0.25) rgba(0, 0, 0, 0.4);
    background: linear-gradient(180deg, rgba(255, 255, 255, 1) 30%, rgba(230, 230, 230, 1) 100%);
    cursor: pointer;
}
button.ctstrg-btn:hover {
    background: #e6e6e6 !important;
}
input[type="file"]::file-selector-button {
    padding: 6px 10px 6px !important;
    box-shadow: inset 0 1px 0 rgb(255 255 255 / 20%),
        0 1px 2px rgb(0 0 0 / 5%);
    border: 1px solid;
    border-radius: 4px;
    border-color: rgba(0, 0, 0, 0.25) rgba(0, 0, 0, 0.25) rgba(0, 0, 0, 0.4);
    background: linear-gradient(180deg, rgba(255, 255, 255, 1) 30%, rgba(230, 230, 230, 1) 100%);
    margin-bottom: 20px;
}

.ctstrg-form {
    padding: 30px;

}

form input[type="submit"] {
    padding: 6px 10px 6px !important;
    box-shadow: inset 0 1px 0 rgb(255 255 255 / 20%),
        0 1px 2px rgb(0 0 0 / 5%);
    border: 1px solid;
    border-radius: 4px;
    border-color: rgba(0, 0, 0, 0.25) rgba(0, 0, 0, 0.25) rgba(0, 0, 0, 0.4);
    background: linear-gradient(180deg, rgba(255, 255, 255, 1) 30%, rgba(230, 230, 230, 1) 100%);
}

form input[type="submit"]:hover {
    background: #e6e6e6
}
form input[type="name"], input[type="email"], input[type="number"] {
    width: 20%;
    box-shadow: inset 0 1px 1px rgb(0 0 0 / 8%);
    transition: border linear .2s,
    box-shadow linear .2s;
    padding: 10px 0;
    margin-bottom: 20px;
    border: 1px solid #cccccc;
    border-radius: 4px;
}
form input[type="name"]:focus,
input[type="email"]:focus,
input[type="number"]:focus {
    border: 1px solid #146f45;
    outline: none;
}