@import url('https://fonts.googleapis.com/css2?family=Monda:wght@400;700&display=swap');

.Test1 {
    background-image:  url('https://kyosk.app/images/memories-banner.jpg');
    background-size: cover;
    display: block;
    width: 100%;
    padding: 144px 0;
}
.test1h3 {
    padding: 40px;
    background-color: rgba(64, 96, 175, .8);
    color: #fff;
    font-size: 30px;
    margin: 0 200px;
    text-align: center;
}


@media only screen and (max-width: 640px) {
    .test1h3 {
        margin: 0px 50px;
        font-size: 20px;
    }
    .Test1 {
        background-size: cover;
        padding: 60px 0;
        background-repeat: no-repeat;
    }
}